import React from 'react';

class TimeSelector extends React.Component {
	state = {value: ''};

	handleSelect = (event) => {
		this.setState({
			value: event.target.value
		}, this.updateSelection);
	}

	updateSelection() {
		this.props.onTimeSelection(this.state.value);
	}

	render() {
		return (
			<div className="headline__align">
				<label for="time-select" className="sr-only">Time select:</label>
				<select value={this.props.currentTime || ''} onChange={this.handleSelect} className="headline__select" id="time-select">
					<option value="day">day</option>
					<option value="night">night</option>
				</select>
			</div>
		);
	}
}

export default TimeSelector;


